@import '../variables.scss';

.slider{
    position: relative;
    display: flex;
    align-items: center;
}

label>input[type="radio"] {
    display: none;
}

label>input[type="radio"]+*::before {
    content: "";
    display: inline-block;
    vertical-align: bottom;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #E4E4E4;
}

label>input[type="radio"]:checked+*::before {
    background-color: $primary;
}

label>input[type="radio"]+* {
    display: inline-block;
    margin-right: 16px;
    font-size: 0;
}

@media screen and (min-width: 768px) {
    label>input[type="radio"]+*::before{
        height: 25px;
        width: 25px;
    }
    label>input[type="radio"]+*{
        margin-right: 30px;
    }
}

.carousel-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
}

.carousel{
    display: grid;
    align-items: center;
    grid-template-columns: 194px 272px 194px 194px;
    column-gap: 20px;
    transform: translateX(-170px);
}

.carousel-item:nth-child(1){
    order: 2;
}

.carousel-item:nth-child(2){
    order: 3;
}

.carousel-item:nth-child(3){
    order: 4;
}

.carousel-item:nth-child(4){
    order: 1;
}

.carousel-catalog-inputs{
    display: block;
    margin: 0 auto;
    margin-top: 15px;
}

.carousel-catalog-inputs label>input[type="radio"]+* {
    display: inline-block;
    margin-right: 15px;
    font-size: 0;
}

.carousel-inputs{
    display: flex;
    justify-content: space-between;
}

.carousel-inputs #left-arrow,
.carousel-inputs #right-arrow{
    border: none;
    background: none;
    height: 60px;
}